// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import squareRoot, {rootOfGrade} from 'root-calculate'
import BigNumber from 'bignumber.js'

const CalculatorComponent = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	padding: 30px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
`
const Result = styled.input`
	width: 100%;
	max-width: 500px;
	font-size: 50px;
	font-weight: bold;
	margin: 10px;
	text-align: left;
	border: none;
`
const Operation = styled.input`
	width: 100%;
	max-width: 500px;
	font-size: 50px;
	font-weight: bold;
	margin: 10px;
	text-align: right;
	border: none;
`
const Buttons = styled.div`
	width: 100%;
	max-width: 500px;
	height: calc(100vh - 200px);
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-gap: 10px;
`
const Button = styled.button`
	font-size: 25px;
`

class Calculator extends Component {
	state = {
		result: '0',
		operation: '',
		endRoot: ''
	}
	insert = (e) => {
		e.persist()
		this.setState(
			prevState => ({
				// operation: prevState.operation + (prevState.operation || !isNaN(Number(e.target.value)) || e.target.value === '√' ? e.target.value : '')
				operation: prevState.operation + e.target.value
			}), () => {
				// this.calc()
			});
	}
	calc = () => {
		/* eslint no-eval: 0 */
		let result = this.state.operation
		console.log(result);
		if (result.includes('√')) {
				result = result.replace(/[0-9.]+√[0-9.]+/g, x => {
					const first = Number(x.replace(/√[0-9.]+/g, ''));
					const second = Number(x.replace(/[0-9.]+√/g, ''));
					console.log(first, second);

					return BigNumber(first).multipliedBy(rootOfGrade(2, second).result || 1) + (rootOfGrade(2, second).root ? `√${rootOfGrade(2, second).root}` : '')
				})
				result = result.replace(/√[0-9.]+/g, (x) => {
					const first = Number(x.replace('√', ''));
					// console.log(rootOfGrade(2, first));
					return squareRoot(first)
				})
		}
		if (result.includes('×')) {
			result = result.replace(/[0-9.]+×[0-9.]+/g, x => {
				const first = Number(x.replace(/×[0-9.]+/g, ''));
				const second = Number(x.replace(/[0-9.]+×/g, ''));
				console.log(first, second);
				return BigNumber(first).multipliedBy(second)
			})
		}
		if (result.includes('÷')) {
			result = result.replace(/[0-9.]+÷[0-9.]+/g, x => {
				const first = Number(x.replace(/÷[0-9.]+/g, ''));
				const second = Number(x.replace(/[0-9.]+÷/g, ''));
				console.log(first, second);
				return BigNumber(first).dividedBy(second)
			})
		}
		if (result.includes('+')) {
			result = result.replace(/[0-9.]+\+[0-9.]+/g, x => {
				const first = Number(x.replace(/\+[0-9.]+/g, ''));
				const second = Number(x.replace(/[0-9.]+\+/g, ''));
				console.log(first, second);
				return BigNumber(first).plus(second)
			})
		}
		if (result.includes('-')) {
			result = result.replace(/[0-9.]+-[0-9.]+/g, x => {
				const first = Number(x.replace(/-[0-9.]+/g, ''));
				const second = Number(x.replace(/[0-9.]+-/g, ''));
				console.log(first, second);
				return BigNumber(first).minus(second)
			})
		}
		
		this.setState({result})
	}
	render() {
		return (
			<CalculatorComponent>
				<Result readOnly value={!this.state.result ? '0' : this.state.result} />
				<Operation readOnly value={this.state.operation} />
				<Buttons>
					<Button value="7" onClick={this.insert}>7</Button>
					<Button value="8" onClick={this.insert}>8</Button>
					<Button value="9" onClick={this.insert}>9</Button>
					<Button value="÷" onClick={this.insert}>÷</Button>

					<Button value="4" onClick={this.insert}>4</Button>
					<Button value="5" onClick={this.insert}>5</Button>
					<Button value="6" onClick={this.insert}>6</Button>
					<Button value="×" onClick={this.insert}>×</Button>

					<Button value="3" onClick={this.insert}>3</Button>
					<Button value="2" onClick={this.insert}>2</Button>
					<Button value="1" onClick={this.insert}>1</Button>
					<Button value="-" onClick={this.insert}>-</Button>
					
					<Button value="0" onClick={this.insert}>0</Button>
					<Button value="." onClick={this.insert}>.</Button>
					<Button value="+" onClick={this.insert}>+</Button>
					<Button value="=" onClick={this.calc}>=</Button>
					<Button value="√" onClick={this.insert}>√</Button>

					{/* <Button value="√" onClick={this.insert}>√</Button> */}
				</Buttons>
			</CalculatorComponent>
		);
	}
}

export default Calculator